@font-face {
  font-family: "BebasNeue";
  src: local("BebasNeue Regular"), local("BebasNeue-Regular");
  src: url("../fonts/bebasneue/bebasneueregular.woff2") format("woff2"), url("../fonts/bebasneue/bebasneueregular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap; }

@font-face {
  font-family: "BebasNeue";
  src: local("BebasNeue Bold"), local("BebasNeue-Bold");
  src: url("../fonts/bebasneue/bebasneuebold.woff2") format("woff2"), url("../fonts/bebasneue/bebasneuebold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap; }

.page {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0;
  background-color: #F2F2F2;
  font-family: 'Open Sans', sans-serif;
  color: #48555B;
  font-size: 13px;
  font-weight: 400;
  line-height: 23px;
  letter-spacing: 0.19px; }

.container {
  min-width: 320px;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 250px 0 250px; }

/*@media (min-width: $tablet-width) {
  .container {
    padding: 0;
  }
}*/
/*@media (min-width: $desktop-width) {
  .container {
    max-width: $max-width;
  }
}*/
html {
  margin: 0;
  padding: 0; }

.img {
  max-width: 100%;
  height: auto; }

.img-wrap {
  display: flex;
  margin: 0;
  padding: 0; }

*,
*::before,
*::after {
  box-sizing: border-box; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

a {
  text-decoration: none; }

.promo .container {
  padding-left: 0;
  padding-right: 0; }

.promo__wrap {
  position: relative;
  height: 100%; }

.promo__img-wrap {
  background-color: #3e5064;
  width: 1440px;
  height: 768px; }

.promo__desc {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 40px;
  padding-bottom: 70px;
  color: #FFFFFF;
  z-index: 2; }

.promo__logo {
  margin-bottom: auto; }

.promo__title {
  margin: 0;
  margin-bottom: 12px;
  font-family: "BebasNeue", Arial, sans-serif;
  font-size: 60px;
  letter-spacing: 5.71px;
  line-height: 100%;
  text-transform: uppercase; }

.promo__slogan {
  margin: 0;
  font-weight: 600;
  font-size: 16px; }

.promo__down {
  margin-top: auto; }

.footer {
  background-color: #171819; }
